.skills-section {
    background-color: #000000;
    color: white;
    padding: 60px;
    font-family: 'Roboto', sans-serif;
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 100vh;
  }
  
  .content {
    flex: 1;
    padding-left: 40px;
    max-width: 600px;
  }
  
  .skills-header {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .skills-header h1 {
    font-size: 3rem;
    margin-right: 20px;
    font-weight: bold;
  }
  
  .header-lines {
    display: flex;
    margin-left: auto;
  }
  
  .line {
    height: 5px;
    margin: 0 5px;
  }
  
  .line1 {
    background-color: #ff0080;
    width: 80px;
  }
  
  .line2 {
    background-color: #00eaff;
    width: 120px;
  }
  
  .line3 {
    background-color: #82f278;
    width: 60px;
  }
  
  .line4 {
    background-color: #ff9f00;
    width: 100px;
  }
  
  .skills-description {
    font-size: 1.2rem;
    line-height: 1.5;
    margin-bottom: 40px;
  }
  
  .globe-container {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-right: 50px; 
  }
  
  #myCanvas {
    background-color: transparent;
    border: none; 
    outline: hidden; 
  }
  
  #tags {
    display: none;
  }
  
  #tags ul {
    list-style-type: none;
  }
  
  #tags ul li {
    display: inline-block;
  }
  
  #tags ul li a {
    color: #00eaff;
    text-decoration: none;
    font-size: 1rem;
    font-weight: bold;
    transition: color 0.3s ease;
  }
  
  #tags ul li a:hover {
    color: #ff0080;
  }
  
  .download-cv-btn {
    display: inline-block;
    margin-top: 20px;
    padding: 10px 20px;
    background-color: transparent;
    color: white;
    text-decoration: none;
    font-weight: bold;
    font-size: 1rem;
    border: 2px solid white;
    border-radius: 5px;
    transition: all 0.3s ease;
    position: relative;
  }
  
  .download-cv-btn:hover {
    background-color: white;
    color: black;
  }
  
  .arrow-icon {
    margin-left: 10px;
    transition: transform 0.3s ease;
  }
  
  .download-cv-btn:hover .arrow-icon {
    transform: translateX(5px);
  }
  